#project-section{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  & > h1{
    width: 100vw;
    height: get-m-vw(52px);
    padding: 0 get-m-vw(20px);

    background: $black;
    color: $sand;
    z-index: 1;
    @include m-bebas-30-bold;
    line-height: get-m-vw(52px);
  }
}

.project-section_content{
  flex-grow: 1;
  padding: get-m-vw(18px);
  background: $sand;
  overflow-y: scroll;
}

.project-section_movie-info{
  display: grid;
  grid-template:
  repeat(3, max-content) / get-m-vw(90px) 1fr;
  grid-column-gap: get-m-vw(10px);

  img{
    width: get-m-vw(90px);
    height: get-m-vw(128px);
  }

  ul{
    @include m-helvetica-12-bold;
    display: grid;
    grid-auto-rows: max-content;
    grid-row-gap: get-m-vw(3px);

    span{ color: rgba($black, 0.7); }
  }

  p{
    grid-column: 1/3;
    margin-top: get-m-vw(18px);
    @include m-helvetica-12-bold;
  }
}

.project-section_development-schedule{
  margin-top: get-m-vw(36px);
  position: relative;
  display: grid;
  grid-auto-rows: max-content;
  grid-row-gap: get-m-vw(20px);

  h2{
    margin-bottom: get-m-vw(-12px);
    @include m-bebas-30-bold;
  }

  div{
    position: relative;

    & > span{
      display: inline-block;
      margin-top: get-m-vw(10px);
      @include m-helvetica-12-bold;
      color: rgba($black, 0.5);
    }
    h3{ @include m-bebas-22-bold; }
    p{
      margin-top: get-m-vw(8px);
      span{
        display: block;
        color: #FF5C43;
      }
      @include m-helvetica-12-bold;
    }

    &:not(:last-child)::before{
      width: 100%;
      height: get-m-vw(20px);
      left: 0; bottom: get-m-vw(-32px);
      position: absolute;
      background: url('~assets/images/broken_line.svg') right top / 100% auto no-repeat;
      content: '';
    }
  }
}
